
body {
    /* background: linear-gradient(to right, #85D8CE, #085078); */
    background: whitesmoke;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    align-item: center;
}
.calculator {
    width: 440px;
    padding: 20px;
    border-radius: 10px;
    margin: 20px auto;
    font-size: 16px;
    background-color: #e0e0e0;
    -webkit-box-shadow: 8px 8px 14px #a4a4a4;
            box-shadow: 8px 8px 14px #a4a4a4;
}
.input {
    width: 420px;
    height: 75px;
    border-radius: 0px;
    border: 1px solid black;
    background-color: #333333;
    color: #d9d9d9;
    padding: 0 5px 0 5px;
    margin: 0 0px 10px 0px;
    font-size: 30px;
}
.input:focus,
  .input:active {
    border-color: #03a9f4;
    -webkit-box-shadow: 0 0 4px #03A9F4;
            box-shadow: 0 0 4px #03A9F4;
    outline: none 0;
}
.button {
    margin: 5px;
    width: 60px;
    height: 60px;
    border: 0px;
    border-radius: 50%;
    color: #242323b9;
    cursor: pointer;
    outline: none;
    -webkit-box-shadow:  8px 8px 14px #a4a4a4,
                -8px -8px 14px #ffffff;
            box-shadow:  8px 8px 14px #a4a4a4,
                -8px -8px 14px #ffffff;
}
.button:active {
    -webkit-transform: translateY(2px);
            transform: translateY(2px)
}
.button:hover {
    -webkit-box-shadow: 14px 14px 14px #a4a4a4,
               -10px -10px 14px #ffffff;
            box-shadow: 14px 14px 14px #a4a4a4,
               -10px -10px 14px #ffffff;
}
.mode {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
}
.equal-sign {
    width: 133px;
}
.toggle-button {
    margin: 0 0 5px;
}

